// Funcin para obtener un ndice aleatorio que no se haya reproducido
function getRandomIndex() {
  var remainingVideos = [];

  // Filtrar los videos que an no se han reproducido
  for (var i = 0; i < videoData.length; i++) {
    if (!playedVideos.includes(i)) {
      remainingVideos.push(i);
    }
  }

  // Obtener un ndice aleatorio de los videos restantes
  var randomIndex = Math.floor(Math.random() * remainingVideos.length);

  // Devolver el ndice del video seleccionado
  return remainingVideos[randomIndex];
}

